@import "base/general";
@import "base/fonts";
@import "base/variables";
@import "base/mixins";
@import "base/typography";
@import "components/sprite";

@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

@import "vendor/libs";
@import "vendor/normalize";

@import "../blocks/modules";

@import "sections/header";
@import "sections/block-book";

$light-color: #fff;
$accent-color: #65e496;
$gray-color: #9191b5;
$bg-light-color: #f5f6fa;
$bg-dark-color: #44444f;

h1,
h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 24px;
}

body {
  font-family: "Roboto Slab", serif;
  font-size: 15px;
  line-height: 24px;
}

.page {
  &__tittle {
    font-size: 48px;
    text-align: center;
    margin-bottom: 25px;
    letter-spacing: 2px;
    @media (max-width: 500px) {
      line-height: 1.2;
    }
  }
  &__subtittle {
    color: $accent-color;
    text-align: center;
    font-size: 18px;
    letter-spacing: 0.9px;
  }
  &--grey {
    padding-top: 162px;
    background-color: $bg-light-color;
    .block-about-text {
      p {
        letter-spacing: 0.8px;
      }
    }
  }
  &-slider {
    background-image: url("../img/svg/slider-bg.svg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    padding-top: 227px;
    margin-top: 157px;
    margin-bottom: 70px;
    @media (max-width: 1020px) {
      background: $bg-dark-color;
      padding-top: 120px;
    }
  }
}

.page-main {
  background-image: url("../img/Bg/top-section-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.wrap {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container {
  padding: 0;
  margin: 0;
}

.block-text {
  max-width: calc(93% - 350px);
  color: $light-color;
  &--width {
    flex-basis: calc(93% - 500px);
  }
  &__tittle {
    font-size: 48px;
    font-weight: 700;
    letter-spacing: 1.8px;
    line-height: 53px;
    margin-bottom: 38px;
    span {
      display: block;
      color: $accent-color;
      font-size: 18px;
      margin-top: 11px;
      letter-spacing: 0.9px;
      line-height: 23px;
    }
  }
  &__subtittle {
    display: block;
    color: $accent-color;
    font-size: 18px;
    margin-bottom: 45px;
  }
  &__name {
    font-family: "Open Sans";
    font-size: 18px;
    letter-spacing: 0.7px;
    margin-bottom: 30px;
  }
  &__description {
    font-family: "Open Sans";
    font-size: 15px;
    line-height: 24px;
    margin-bottom: 24px;
    letter-spacing: 0.7px;
    vertical-align: 50px;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      max-width: 100%;
    }
  }
  &__btn {
    display: block;
    color: #fff;
    text-decoration: none;
    max-width: 140px;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    margin-top: 45px;
    margin-bottom: 20px;
    padding: 11px 0 15px 0;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    max-width: 250px;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      max-width: 250px;
    }
  }
  small {
    font-family: "Open Sans";
    font-size: 12px;
    font-style: italic;
  }
  &-item {
    margin-bottom: 55px;
    &__date {
      font-size: 15px;
      font-family: "Open Sans";
      letter-spacing: 0.8px;
      margin-bottom: 4px;
    }
    &__tittle {
      font-size: 18px;
      letter-spacing: 0.9px;
      margin-bottom: 16px;
    }
    &__desc {
      font-size: 15px;
      font-family: "Open Sans";
      letter-spacing: 0.75px;
    }
    &__btn {
      display: block;
      color: #fff;
      text-decoration: none;
      border-radius: 3px;
      text-align: center;
      font-size: 18px;
      margin-top: 45px;
      margin-bottom: 20px;
      padding: 11px 0 15px 0;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
    }
  }
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 160px;
  @media (max-width: 920px) {
    flex-direction: column;
    padding-bottom: 100px;
  }
  &__logo {
    display: block;
    width: 137px;
    height: 28px;
    background-image: url("img/Logo/Header-logo.png");
  }

  &-list {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    @media (max-width: 800px) {
      padding-left: 0;
      margin-top: 30px;
      margin-left: auto;
      margin-right: auto;
      justify-content: center;
      text-align: center;
    }
    &__item {
      font-family: "Open Sans";
      font-size: 16px;
      margin-left: 22px;
      padding-bottom: 7px;
      border-bottom: 3px solid transparent;
      transition: 0.3s;
      @media (max-width: 800px) {
        margin-bottom: 15px;
      }
      &.active {
        border-bottom: 3px solid $accent-color;
        @media (max-width: 800px) {
          border: none;
        }
      }
      &:hover {
        border-bottom: 3px solid $accent-color;
      }
    }
    &__link {
      text-decoration: none;
      color: $light-color;
      letter-spacing: 0.5px;
    }
  }
}

.block-book {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 110px;
  @media (max-width: 800px) {
    display: block;
  }
  &__text {
    color: $light-color;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      margin-top: 50px;
      max-width: 100%;
    }
    .block-text__tittle {
      margin-bottom: 10px;
      letter-spacing: 1px;
    }
    .block-text__subtittle {
      margin-top: 0;
      margin-bottom: 40px;
      letter-spacing: 1px;
    }
    .block-text__btn {
      margin-bottom: 14px;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #58b988 inset;
    }
  }
  &-demo {
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      width: 75%;
      height: 75%;
      @media (max-width: 700px) {
        padding-bottom: 25px;
      }
    }
    &__tittle {
      font-size: 36px;
      position: absolute;
      top: 122px;
      left: 133px;
      letter-spacing: 0.7px;
      text-transform: uppercase;
      span {
        font-family: "Open Sans";
        font-size: 14px;
        color: $accent-color;
        display: block;
        position: absolute;
        top: 33px;
        text-transform: none;
        width: 225px;
      }
    }
    &__author {
      font-family: "Open Sans";
      font-size: 14px;
      color: $accent-color;
      position: absolute;
      bottom: 52px;
      left: 88px;
      letter-spacing: 0.7px;
    }
  }
}

.block-author {
  display: flex;
  justify-content: space-between;
  padding-top: 90px;
  @media (max-width: 800px) {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    display: block;
  }
  &--1 {
    padding-top: 175px;
    @media (max-width: 700px) {
      padding-top: 75px;
    }
  }
  &--2 {
    margin-bottom: 147px;
    .block-ava {
      margin-bottom: 50px;
    }
    .block-text--width {
      max-width: 75%;
    }
    .block-text-item__btn {
      margin-left: auto;
      margin-right: auto;
    }
  }

  .block-text {
    color: #000;
    flex-basis: calc(93% - 500px);
    @media (max-width: 800px) {
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 75px;
    }
    &__name {
      margin-bottom: 19px;
    }
    @media (max-width: 1020px) {
      flex-basis: calc(93% - 315px);
    }
    &__tittle {
      margin-bottom: 38px;
      line-height: 53px;
      span {
        margin-top: 10px;
        margin-bottom: 0;
      }
    }
    &__btn {
      max-width: 240px;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #b2f1be inset;
    }
  }
  .block-text--width {
    margin-left: 168px;
    @media (max-width: 1020px) {
      margin-left: 0;
    }
  }

  .block-ava {
    @media (max-width: 800px) {
      width: 75%;
      height: 75%;
      display: block;
    }
    &__img {
      width: 438px;
      height: 466px;
      position: absolute;
      padding-top: 32px;
      @media (max-width: 1020px) {
        width: 300px;
        height: 320px;
      }
    }
    &__svg {
      margin-top: -15px;
      padding-left: 47px;
      @media (max-width: 1020px) {
        width: 300px;
        height: 412px;
      }
    }
    &__img--left {
      width: 435px;
      height: 606px;
      padding-top: 49px;
      padding-left: 50px;
      @media (max-width: 1020px) {
        padding-top: 30px;
        height: 417px;
      }
    }
    &__svg--left {
      padding-left: 0;
      @media (max-width: 1020px) {
        height: 505px;
      }
    }
  }
}

.block-counter {
  margin-top: 166px;
  margin-bottom: 160px;
  .wrap {
    display: flex;
    justify-content: space-around;
    @media (max-width: 750px) {
      flex-direction: column;
    }
  }
  p {
    font-family: "Open Sans";
    text-align: center;
    font-size: 18px;
    @media (max-width: 750px) {
      margin-bottom: 80px;
    }
    span {
      font-family: "Roboto Slab";
      display: block;
      font-weight: 100;
      font-size: 80px;
      color: $accent-color;
      margin-bottom: 45px;
      @media (max-width: 500px) {
        line-height: 1.2;
      }
    }
  }
}

.block-about {
  display: flex;
  justify-content: space-between;
  margin-top: 82px;
  padding-bottom: 230px;
  @media (max-width: 1020px) {
    flex-direction: column;
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
  }
  h3 {
    letter-spacing: 1.2px;
    margin-bottom: 20px;
  }
  p {
    font-family: "Open Sans";
    letter-spacing: 0.6px;
  }
  &-text {
    flex-basis: calc(100% - 690px);
    &__btn {
      width: 135px;
      margin-top: 35px;
      border: none;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #b2f1be inset;
    }
    .feature {
      display: flex;
      margin-top: 30px;
      height: 25px;
      align-items: center;
      vertical-align: middle;
      .feature-tittle {
        font-family: "Roboto Slab";
        font-weight: 700;
        letter-spacing: 3px;
      }
      &-list {
        display: flex;
        list-style-type: none;
        padding-left: 23px;
        padding-top: 5px;
        &__item {
          margin-right: 12px;
        }
        &__icon {
          font-size: 18px;
          color: $accent-color;
        }
      }
    }
  }
  &-table {
    flex-basis: 585px;
    @media (max-width: 1020px) {
      flex-basis: 0px;
      margin-top: 50px;
    }
    &__list {
      margin: 25px 0 0 0;
      padding: 0;
      counter-reset: list;
      column-count: 2;
      column-gap: 19px;
      @media (max-width: 800px) {
        column-count: auto;
      }
      & > li {
        position: relative;
        padding-left: 37px;
        counter-increment: list;
        list-style-type: none;
        margin-bottom: 8px;
        letter-spacing: 0.7px;
        &::before {
          content: counter(list) ". ";
        }
        &::after {
          content: "+";
          font-family: "Open Sans";
          font-size: 15px;
          line-height: 22px;
          position: absolute;
          top: 1px;
          left: 0;
          background-color: #5fc9b2;
          color: $light-color;
          padding: 0px 7px;
        }
      }
      &--next {
        counter-reset: list 10;
        position: relative;
        display: none;
        &::before {
          content: "";
          width: 100px;
          height: 2px;
          background-color: #5fc9b2;
          display: block;
          position: absolute;
          top: -16px;
        }
      }
    }
  }
}

.other-books {
  padding-top: 160px;
  .page-tittle {
    margin-top: 155px;
  }
  &__image {
    margin-bottom: 25px;
    @media (max-width: 800px) {
      width: 75%;
      height: 75%;
    }
  }
  &__list {
    display: flex;
    justify-content: space-around;
    list-style-type: none;
    text-align: center;
    padding-top: 75px;
    padding-left: 0;
    @media (max-width: 1020px) {
      flex-direction: column;
    }
  }
  &__item {
    @media (max-width: 1020px) {
      margin-bottom: 50px;
    }
    .block-about__text {
      p {
        font-family: "Open Sans";
        font-size: 15px;
        margin-top: 15px;
        max-width: 330px;
        margin-bottom: 25px;
        @media (max-width: 1020px) {
          display: block;
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }
  &__link {
    color: $accent-color;
    text-transform: uppercase;
    text-decoration: none;
    line-height: 1;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    position: relative;
    &:after {
      position: absolute;
      top: 20px;
      display: block;
      content: "";
      height: 2px;
      width: 0%;
      background-color: #65e496;
      transition: width 0.3s ease-in-out;
    }
    &:hover:after,
    &:focus:after {
      width: 100%;
    }
  }
  .block-text__btn {
    margin-left: auto;
    margin-right: auto;
    margin-top: 75px;
    max-width: 220px;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #b2f1be inset;
    @media (max-width: 1020px) {
      margin-top: 25px;
    }
  }
}

.block-text--width {
  margin-left: 168px;
  padding-top: 0px;
}

.block-text-item__btn {
  max-width: 135px;
  margin-top: 27px;
}

.block-text {
  margin-left: 0;
}

.block-slider {
  text-align: center;
  position: relative;
  padding-bottom: 200px;
  @media (max-width: 1020px) {
    padding-bottom: 150px;
  }
  h3 {
    color: $accent-color;
    margin-top: 30px;
    margin-bottom: 4px;
    letter-spacing: 1.5px;
  }
  &__desc {
    color: $gray-color;
    font-family: "Open Sans";
    line-height: 27px;
    max-width: 760px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 21px;
  }
  &-ava {
    position: relative;
    width: 152px;
    height: 152px;
    display: block;
    margin: 0 auto 27px;
  }
  &__expert {
    letter-spacing: 0.75px;
    margin-bottom: 32px;
  }
  &-ava {
    position: relative;
    width: 152px;
    height: 152px;
    display: block;
    margin: 0 auto 0;
  }
  &-bg {
    position: absolute;
    display: block;
    background-image: url("../img/Icons/feedback.png");
    background-repeat: no-repeat;
    height: 42px;
    width: 48px;
    top: 0;
    right: -15px;
  }
  &-thumb {
    display: block;
    margin: 0 auto 27px;
    position: relative;
    overflow: hidden;
    width: 152px;
    height: 152px;
    border-radius: 100%;
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style-type: none;
  padding: 0;
  & > li {
    margin: 0 5px;

    & > button {
      text-indent: -9999px;
      width: 8px;
      height: 8px;
      border: none;
      background-color: $accent-color;
      border-radius: 100%;
      line-height: 8px;
      font-size: 0px;
      cursor: pointer;
      padding: 5px;
      &:focus {
        outline: none;
      }
    }
  }
  & > li.slick-active {
    & > button {
      height: 8px;
      width: 37px;
      border-radius: 10px;
    }
  }
}

.block-blog {
  display: flex;
  justify-content: space-around;
  @media (max-width: 900px) {
    flex-direction: column;
    margin-bottom: 40px;
  }
  &-item {
    padding-bottom: 75px;
    flex-basis: 32%;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: 900px) {
      flex-direction: column;
      margin-bottom: 40px;
      max-width: 350px;
    }
  }
  h4 {
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 5px;
  }
  &-img {
    width: 100%;
    height: 224px;
    overflow: hidden;
    position: relative;
    margin-bottom: 15px;
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
  &-info {
    font-family: "Open Sans";
    font-size: 15px;
    margin-bottom: 10px;
    flex-wrap: wrap;
    a {
      color: $accent-color;
      text-decoration: none;
    }
  }
  &__desc {
    font-family: "Open Sans";
    font-size: 15px;
    max-width: 400px;
    letter-spacing: 0.7px;
  }
  &-page__btn {
    display: block;
    margin: 7px auto 0;
    padding: 13px 0;
    max-width: 265px;
  }
}
.page-blog {
  padding-top: 150px;
  padding-bottom: 160px;
  .page__tittle,
  .page__subtittle {
    display: block;
  }
  .page__subtittle {
    margin-bottom: 85px;
    letter-spacing: 1.1px;
  }
}

.block-letter {
  display: flex;
  justify-content: space-between;
  margin-top: 160px;
  margin-bottom: 155px;
  @media (max-width: 800px) {
    flex-direction: column;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  &-item {
    flex-basis: 49%;
    @media (max-width: 800px) {
      margin-bottom: 35px;
    }
  }
  &__tittle {
    text-align: left;
    letter-spacing: 1.9px;
    margin-top: 8px;
    line-height: 1;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      margin-top: 0;
      text-align: center;
    }
  }
  &__text {
    font-family: "Open Sans";
    font-size: 15px;
    letter-spacing: 0.75px;
  }
  &__input {
    box-sizing: border-box;
    width: 100%;
    height: 48px;
    background-color: $bg-dark-color;
    border: none;
    border-radius: 3px;
    font-family: "Open Sans";
    font-weight: 700;
    letter-spacing: 0.6px;
    color: $gray-color;
    padding-left: 30px;
    margin-bottom: 20px;
    @media (max-width: 800px) {
      padding-left: 0;
    }
    &::placeholder {
      font-family: "Open Sans";
      font-weight: 700;
      letter-spacing: 0.6px;
      color: $gray-color;
      @media (max-width: 800px) {
        text-align: center;
      }
    }
  }
  &__btn {
    display: block;
    color: #fff;
    text-decoration: none;
    width: 100%;
    border: none;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    padding: 11px 0 15px 0;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #b2f1be inset;
  }
}

.footer {
  background-color: $bg-dark-color;
  color: $gray-color;
  padding: 155px 0;
}

.block-footer {
  display: flex;
  justify-content: space-between;
  font-family: "Open Sans";
  @media (max-width: 800px) {
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    justify-content: center;
  }
  & > div {
    max-width: 375px;
    @media (max-width: 800px) {
      flex-direction: column;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      justify-content: center;
      padding-bottom: 75px;
    }
  }
  & > p {
    letter-spacing: 1px;
  }

  &__logo {
    display: block;
    width: 137px;
    height: 28px;
    background-image: url("../img/Logo/Footer-logo.png");
    margin-bottom: 25px;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
    }
    & > p {
      max-width: 365px;
    }
  }
  &__tittle {
    font-family: "Roboto Slab";
    font-size: 18px;
    color: $accent-color;
    text-transform: uppercase;
    margin-bottom: 21px;
    padding-top: 7px;
  }
  &__list {
    list-style-type: none;
    padding-left: 0;
    line-height: 26px;
    letter-spacing: 1.5px;
    margin: 0;
  }
  &__social {
    display: flex;
    list-style-type: none;
    padding-left: 0;
    font-size: 15px;
    @media (max-width: 800px) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      justify-content: center;
      padding-bottom: 75px;
    }
    & > li {
      margin-right: 12px;
      & > a {
        color: $accent-color;
      }
    }
  }
  &__copyright {
    margin-top: 25px;
  }
}
